.intro {
    padding-top: 220px;
    .xmd-block({
        padding-top: 105px;
    });
    &__store {
        display: flex;
        align-items: center;
        color: #fff;
        .xmd-block({
            flex-direction: column;
            margin: 0 auto;
        });
        &--title {
            .medium();
            max-width: 76px;
            text-align: center;
            .xmd-block({
                .bold();
                max-width: none;
                color: #fff6c0;
            });
        }
        &--wrap {
            margin-left: 35px;
            &::before, &::after {
                content: '';
                width: 100%;
                order: 1;
                display: none;
                .xmd-block({
                    display: block;
                });
            }
            .lg-block({
                margin-left: 30px;
            });
            .xmd-block({
                margin: 20px 0 0 0;
                display: flex;
                justify-content: center;
                flex-wrap: wrap;
            });
        }
        &--img {
            display: none;
            margin-top: 30px;
            .xmd-block({
                display: block;
            });
        }
        &--item {
            .medium();
            position: relative;
            padding-left: 33px;
            text-decoration: underline;
            margin: 0 15px;
            transition: .3s all;
            font-size: 15px;
            .lg-block({
                padding-left: 25px;
                margin: 0 10px;
                font-size: 14px;
            });
            .xmd-block({
                font-size: 15px;
                padding-left: 30px;
                margin: 0 15px;
            });
            &:nth-child(n+4) {
                order: 1;
                margin-top: 10px;
            }
            &:hover {
                color: #f5d06d;
            }
            &:first-child {
                margin-left: 0;
            }
            &:last-child {
                margin-right: 0;
            }
            &::after {
                content: '';
                position: absolute;
                top: 0; left: 0;
                background: url(../img/store.png) no-repeat;
                width: 20px;
                height: 19px;
            }
        }
    }
    &__content {
        .xmd-block({
            display: flex;
            flex-direction: column;
        });
    }
    &__title {
        margin-top: 60px;
        text-align: center;
        color: #fff;
        .xmd-block({
            order: 2;
        });
        &--h1 {
            .philbold();
            font-size: 85px;
            text-transform: uppercase;
            .lg-block({
                font-size: 70px;
            });
            span {
                font-size: 107px;
                .lg-block({
                    font-size: 90px;
                });
                &:nth-child(2),
                &:last-child {
                    color: #f5d06d;
                }
                &:nth-child(3) {
                    position: relative;
                    padding: 0 15px;
                    &::after,
                    &::before{
                        content: '';
                        position: absolute;
                        top: 50%; 
                        transform: translate(0, -50%);
                        width: 1px;
                        height: 80px;
                        background-color: #ffffff;
                        .lg-block({
                            display: none;
                        });
                    }
                    &::after {
                         right: 0;
                    }
                    &::before {
                        left: 0;
                    }
                }
            }
        }
        &--desc {
            .phil();
            font-size: 45px;
            padding-top: 5px;
                span {
                    display: inline-block;
                    width: 112px;
                    line-height: 60px;
                    border-radius: 30px;
                    background-color: #ff2727;
                }
        }
    }
    &__items {
        margin-top: 30px;
        display: flex;
        align-items: flex-start;
        flex-wrap: wrap;
        max-width: 400px;
        .lg-block({
            max-width: none;
            margin-top: 25px;
            justify-content: center;
        });
        .xmd-block({
            order: 1;
            margin-top: -10px;
        });
    }
    &__item {
        &--none {
            .lg-block({
               display: none;
            });
        }
        &--for {
           position: relative;
            top: -20px;
            .lg-block({
                top: auto;
            });
        }
    }
    &__form {
        margin-top: -220px;
        margin-left: auto;
        display: flex;
        flex-direction: column;
        max-width: 410px;
        color: #fff;
        .lg-block({
            margin-top: 0px;
        });
        .xmd-block({
            order: 3;
        });
        &--top {
            .phil();
            font-size: 25px;
        }
        &--time {
            width: 100%;
            height: 100px;
            background-color: #000;
            margin-bottom: 60px;
        }
        &--trust {
            &1 {
                p {
                    .philbold();
                    font-size: 25px;
                    span {
                        font-size: 39px;
                    }
                }
                position: relative;
                padding-left: 90px;
                &::after {
                    content: '';
                    position: absolute;
                    top: 0; left: 0;
                    background: url(../img/intro-form1.png) no-repeat;
                    width: 108px;
                    height: 109px;
                }   
            }
            &2 {
                p {
                    .phil();
                    font-size: 23px;
                }
                position: relative;
                padding-top: 30px;
                padding-left: 90px;
                &::after {
                    content: '';
                    position: absolute;
                    top: 0; left: 0;
                    background: url(../img/intro-form2.png) no-repeat;
                    width: 89px;
                    height: 134px;
                }   
            }
        }
        &--oldprice {
            .phil();
            margin-top: 30px;
            border-radius: 34px;
            background-color: #120903;
            color: #fff;
            width: 370px;
            height: 67px;
            position: relative;
            display: flex;
            align-items: center;
            padding-left: 30px;
            .small {
                display: inline-block;
                font-size: 13px;
                s {
                    font-size: 35px;
                }
            }
            .price {
                margin-left: 12px;
                display: inline-block;
                font-size: 50px;
                color: #f5d06d;
            }
            span {
                position: absolute;
                top: -5px; right: 5px;
                font-size: 22px;
                width: 60px;
                border-radius: 18px;
                background-color: #ff2727;
                text-align: center;
                line-height: 22px;
                padding: 9px 7px;
            }
        }
        &--btn {
            margin-top: 30px;
            .btn();
        }
    }
}