.footer {
    background: url(../img/footer.png) no-repeat;
    background-position: center;
    min-height: 1266px;
    padding-top: 110px;
    &_title {
        text-align: center;
        color: #fff;
        .h2();
        span {
            font-size: 80px;
            span {
                color: #f5d06d;
            }
        }
    }
    &__form {
        margin-top: 65px;
    }
    &_bottom {
        display: flex;
        justify-content: space-between;
        color: #fff;
        position: relative;
        padding-top: 150px;
        &__left {
            z-index: 10;
        }
        &__nav {
            display: flex;
            justify-content: space-between;
            .div {
                display: flex;
                flex-direction: column;
                &:nth-child(2) {
                    margin-left: 55px;
                }
            }
        }
        &__a {
            .phil();
            padding: 10px 0;
            transition: .3s all;
            &:hover {
                color: #f5d06d;
            }
        }
        &__center {
            z-index: 10;
            position: absolute;
            bottom: -25px; left: 50%;
            transform: translate(-50%,0);
        }
        &__right {
            padding-top: 15px;
            z-index: 10;
            display: flex;
            align-items: flex-start;
            .visa_img {
                margin-top: 10px;
            }
        }
        &--double {
            margin-left: 25px;
            padding-top: 0;
        }
        &--logo {
            position: absolute;
            top: 75px; left: 50%;
            transform: translate(-50%,0);
            z-index: 0;
        }
    }
}
.double {
    &__desc {
        color: rgba(255, 236, 127, 0.49);
        font-size: 12px;
    }
    &__tel {
        .phil();
        display: inline-block;
        margin-top: 9px;
        font-size: 25px;
        .lg-block({
            font-size: 22px;
        });
        &:hover {
            text-decoration: underline;
        }
    }
    &__callback {
        .bold();
        display: inline-block;
        text-align: center;
        padding: 11px 24px;
        margin-top: 10px;
        border-radius: 18px;
        color: #000;
        background-color: #ffd956;
        background-image: linear-gradient(to top, #f5c13d 0%, #f8ebb1 100%);
        transition: .5s all;
        .lg-block({
            padding: 10px 15px;
            font-size: 15px;
        });
        &:hover {
            .hover();
        }
    }
}