.map {
    &_title {
        margin-top: 70px;
        text-align: center;
        .h2();
        color: #fff;
        &::after {
            .h2_after();
            
        }
    }
    &_content {
        background: url(../img/map.png) no-repeat;
        background-size: cover;
        background-position: center bottom;
        min-height: 933px;
    }
    &_wrap {
        margin-top: 60px;
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        &::before, &::after {
            content: '';
            width: 100%;
            order: 1;
        }
    }
    &_item {
        height: 250px;
        width: 270px;
        background-color: #ce261b;
        background-image: linear-gradient(to top, #951818 0%, #ce261b 100%);
        &:nth-child(n + 5) {
            order: 1;
            margin-top: 220px;
        }
        &--title {
            text-align: center;
            padding-top: 25px;
            .phil();
            span {
                color: #ffd956;
                font-size: 25px;
                padding-bottom: 15px;
            }
            color: #fff;
            font-size: 18px;
        }
        &--circle {
            border-radius: 50%;
            border: 1px solid #ce261b;
            background-color: #fff;
            width: 270px;
            height: 270px;
            position: relative;
            top: 50px;
            overflow: hidden;
            #map {
                height: 150%;
            }
        }
    }
}

@media (min-width:1856px) {
    .map_content {
        background-size: contain;
        min-width: 1856px;
        position: relative;
        left: 50%;
        transform: translate(-50%,0);
    }
}