.why {
    background: url(../img/why.png) no-repeat;
    background-size: cover;
    background-position: center center;
    min-height: 895px;
    padding-bottom: 35px;
    position: relative;
    left: 50%;
    transform: translate(-50%,0);
    margin-top: 70px;
    &_content {
        max-width: 1370px;
        margin: 0 auto;
        width: 100%;
    }
    &_title {
        .h2();
        text-align: center;
        padding-top: 65px;
        &::after {
            .h2_after();
            background-color: #951818;
        }
    }
    &_wrap {
        margin-top: 60px;
        display: flex;
        flex-wrap: wrap;
        align-items: flex-start;
        justify-content: space-between;
        .lg-block({
            justify-content: center;
            
        });
        &::before, &::after {
            content: '';
            width: 100%;
            order: 1;
        }
    }
    &_item {
        text-align: center;
        .lg-block({
            flex-basis: 25%;
            display: flex;
            flex-direction: column;
            align-items: center;

        });
        &--img {
            .lg-block({
                width: 170px;
                height: 170px;
            });
        }
        &--h5 {
            margin-top: 25px;
            .philbold();
            max-width: 200px;
            padding: 0 15px;
            font-size: 18px;
        }
        &:nth-child(n + 6) {
            order: 1;
            margin-top: 38px;
            .lg-block({
                order: 0;
            });
        }
    }
}

@media (min-width:1683px) {
    .why {
        min-width: 1683px;
        &_content {
            max-width: 1370px;
            margin: 0 auto;
            width: 100%;
        }
    }
}