.about {
    margin-top: 130px;
    background: url(../img/about.png) no-repeat;
    background-size: cover;
    background-position: center center;
    min-height: 1527px;
    position: relative;
    left: 50%;
    transform: translate(-50%,0);
    &_wrap {
        display: flex;
        align-items: flex-start;
        position: relative;
        min-height: 1000px;
    }
    &_right {
        display: flex;
        flex-direction: column;
        position: absolute;
        top: 0px; right: 70px;
        .lg-block({
            right: 0px;
        });
        &__item {
            position: relative;
            &:nth-child(1) {
                top: -30px;
            }
            &:nth-child(2) {
                top: -105px;
            }
            &:nth-child(3) {
                top: -160px;
            }
            &:nth-child(4) {
                top: -210px;
            }
            img {
                z-index: 2;
                position: relative;
            }
        }
        &--title {
            .extrabold();
            font-size: 23px;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%);
            z-index: 3;
        }
        &--text {
            .medium();
            text-align: center;
            font-size: 23px;
            position: absolute;
            top: calc(50%+12px);
            left: 50%;
            transform: translate(-50%);
            z-index: 3;
        }
    }
    
    &_center {
        position: absolute;
        top: -100px;
        right: 190px;
        z-index: 1;
        width: 776px;
        height: 1075px;
        .lg-block({
            right: 0;
        });
    }
    &_left {
        position: relative;
        margin-top: 30px;
        &--top {
            display: flex;
            flex-wrap: wrap;
            align-items: flex-start;
            position: relative;
            left: -20px;
            &::after,&::before {
                content: '';
                width: 100%;
                order: 1;
            }
        }
        &--bottom {
            margin-top: -10px;
            color: #fff;
            position: relative;
            width: 603px;
            height: 589px;
            z-index: 2;
            img {
                position: absolute;
                z-index: -1;
                top: 0; left: 0;
            }
        }
        &__item {
            &:nth-child(n + 3) {
                order: 1;
            }
        }
        &__title {
            margin-left: 35px;
            padding-top: 78px;
            .extrabold();
            font-size: 26px;
            span {
                font-size: 53px;
            }
        }
        &__ul {
            margin-left: 35px;
            margin-top: 45px;
        }
        &__li {
            margin: 16px 0;
            padding-left: 25px;
            position: relative;
            &:nth-child(1) {
                
            }
            &:nth-child(2) {
                &::before {
                    content: '2'; 
                }
            }
            &:nth-child(3) {
                &::before {
                    content: '3'; 
                }

            }
            &:nth-child(4) {
                &::before {
                    content: '4'; 
                }
            }
            &:nth-child(5) {
                &::before {
                    content: '5'; 
                }
            }
            &:nth-child(6) {
                position: relative;
                top: -3px;
                &::before {
                    content: '6'; 
                }
            }
            &:nth-child(7) {
                &::before {
                    content: '7'; 
                }
            }
            &:nth-child(8) {
                &::before {
                    content: '8'; 
                }
            }
            &:nth-child(9) {
                &::before {
                    content: '8'; 
                }
            }
            &::before {
                content: '1';
                position: absolute;
                top: 0; left: 0;
                font-size: 12px;
            }
            p {
                font-size: 21px;
            }
        }
    }
}
@media (min-width:1683px) {
    .about {
        min-width: 1683px;
        &_content {
            max-width: 1370px;
            margin: 0 auto;
            width: 100%;
        }
    }
}