.main {
    background: url(../img/bg1.png) no-repeat;
    background-position: top;
    min-height: 10250px;
    .xmd-block({
        background: url(../img/bg2.png) no-repeat;
        background-position: top center;
        min-height: 16577px;
    });
}

.header {
    position: relative;
    color: #fff;
    &_wrap {
        padding: 0 20px;
        width: 100%;
        position: absolute;
        top: 0; left: 0;
        display: flex;
        justify-content: space-between;
        .xmd-block({
            padding-left: 10px;
            padding-right: 10px;
            min-width: 768px;
            background: url(../img/header-bg.png) no-repeat;
            align-items: center;
            min-height: 90px;
        });
    }
    &_left {
        display: flex;
        justify-content: space-between;
        .xmd-block({
            order: 2;
            margin-left: 100px;
        });
        .tab {
            display: none;
            position: relative;
            .xmd-block({
                display: block;
            });
        }
        .desk {
            .xmd-block({
                display: none;
            });
        }
        .bold();
        &__item {
            text-align: center;
            background-color: #fff;
            font-size: 15px;
            width: 100px;
            position: relative;
            padding-top: 20px;
            .xmd-block({
                background-color: transparent;
                width: auto;
                padding-top: 0;
            });
            &:last-child {
                margin-left: 30px;
            }
            &::after {
                content: '';
                position: absolute;
                bottom: -30px;
                left: 0;
                background-color: #fff;
                width: 100px;
                height: 50px;
                clip-path: polygon(100% 45%, 0 45%, 0 100%);
                .xmd-block({
                    display: none;
                });
            }
        }
        &__share {
            color: #000;
            text-decoration: underline;
        }
        &__basket {
            color: #b21f1a;
        }
        &__figure {
            position: relative;
            .xmd-block({
                display: none;
            });
        }
        &__number {
            position: absolute;
            top: -35px; right: -10px;
            width: 28px;
            height: 28px;
            background-color: #ffd956;
            border-radius: 50%;
            .xmd-block({
                top: 0; right: -5px;
            });
            span {
                color: #000;
                position: absolute;
                top: 50%; left: 50%;
                transform: translate(-50%,-50%);
            }
        }
    }
    &_nav {
        display: flex;
        min-width: 850px;
        justify-content: space-between;
        position: relative;
        padding: 35px 0px 0;
        padding-left: 65px;
        transition: .3s all;
        &.active {
            top: 70px;
        }
        .lg-block({
            padding: 35px 20px;
            min-width: 800px;
        });
        .xmd-block({
            padding: 0;
            min-width: 1px;
            order: 1;
            flex-direction: column;
            background-color: #951818;
            position: absolute;
            top: -200px; left: 20px;
            z-index: 12;
        });
        &__left,
        &__right {
            display: flex;
            z-index: 10;
            .xmd-block({
                flex-direction: column;
            });
        }
        &__logo {
            position: absolute;
            top: 0; left: 50%;
            transform: translate(-50%);
            z-index: 0;
            .lg-block({
                width: 650px;
                left: 48%;
            });
            .xmd-block({
                display: none;
            });
        }
        &__li {
            margin: 0 9px;
            .xmd-block({
                margin: 10px 0;
                text-align: center;
                padding: 0 15px;
            });
            &:first-child {
                margin-left: 0;
            }
            &:last-child {
                margin-right: 0;
            }
            .lg-block({
                margin: 0 8px;
            });
        }
        &__a {
            .phil();
            font-size: 22px;
            transition: .3s all;
            &:hover {
                color: #f5d06d;
            }
            .lg-block({
                font-size: 20px;
            });
        }
    }
    &_right {
        display: flex;
        flex-direction: column;
        margin-left: 115px;
        padding-top: 15px;
        .lg-block({
            margin-left: 0;
        });
        .xmd-block({
            order: 3;
            padding-top: 0;
        });
        &__desc {
            color: rgba(255, 236, 127, 0.49);
            font-size: 12px;
            .xmd-block({
                display: none;
            });
        }
        &__tel {
            .phil();
            display: inline-block;
            margin-top: 9px;
            font-size: 25px;
            position: relative;
            .xmd-block({
                padding-left: 45px;
                font-size: 35px;
                text-decoration: underline;
            });
            .lg-block({
                font-size: 22px;
            });
            &:hover {
                text-decoration: underline;
                .xmd-block({
                    color: #f5d06d;
                });
            }
            &::after {
                content: '';
                display: none;
                .xmd-block({
                    display: block;
                    background: url(../img/tel.png) no-repeat;
                    width: 25px;
                    height: 25px;
                    position: absolute;
                    top: 5px; left: 0;
                });
            }
        }
        &__callback {
            .bold();
            display: inline-block;
            text-align: center;
            padding: 11px 24px;
            margin-top: 10px;
            border-radius: 18px;
            color: #000;
            background-color: #ffd956;
            background-image: linear-gradient(to top, #f5c13d 0%, #f8ebb1 100%);
            transition: .5s all;
            
            .lg-block({
                padding: 10px 15px;
                font-size: 15px;
            });
            .xmd-block({
                display: none;
            });
            &:hover {
                .hover();
            }
        }
    }
}