@keyframes blink {
  0% {
    top: 12px;
    left: 0;
  }

  100% {
    top: 12px;
    left: 80%;
  }
}

.blink {
  top: 12px;
  display: none;
  animation: blink 1s;
  animation-iteration-count: infinite;
  position: absolute;
  background-color: #fff;
  width: 20px;
  height: 45px;
  transform: skewX(-30deg);
  z-index: 10;
}

.intro__form--btn:hover .blink {
  display: block;
}

.btn_mnu {
  width: 50px;
  height: 50px;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flex;
  display: -o-flex;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  cursor: pointer;
  position: relative;
  position: absolute;
  top: 15px;
  left: 20px;
  z-index: -1;
  opacity: 0;
}

.btn_row {
  height: 3px;
  border-radius: 2px;
  width: 82%;
  margin: 2px 0;
  background-color: #fff;
  position: relative;
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
}

.btn_mnu.active .btn_row:first-child {
  -webkit-transform: rotate(-135deg);
  -ms-transform: rotate(-135deg);
  -o-transform: rotate(-135deg);
  transform: rotate(-135deg);
  top: 6px;
}

.btn_mnu.active .btn_row:last-child {
  -webkit-transform: rotate(-225deg);
  -ms-transform: rotate(-225deg);
  -o-transform: rotate(-225deg);
  transform: rotate(-225deg);
  top: -6px;
}

.btn_mnu.active .btn_row:nth-child(2) {
  opacity: 0;
}

