.already {
    background: url(../img/already.png) no-repeat;
    background-position: center center;
    background-size: cover;
    min-height: 351px;
    margin-top: 95px;
    text-align: center;
    &_title {
        .philbold();
        padding-top: 45px;
        font-size: 50px;
        span {
            text-decoration: underline;
        }
    }
    &_btn {
        .btn();
        color: #fff;
        margin-top: 25px;
    }
}