.gray {
    margin-top: 30px;
    &_wrap {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        .lg-block({
            justify-content: space-around;
        }); 
    }
    &_left {
        margin-top: 20px;
        &--text {
            line-height: 1.5;
            max-width: 450px;
            padding: 0 10px;
           .lg-block({
               padding-top: 25px;
               max-width: 600px;
            }); 
        }
    }
    &_center {
        &--desk {
            display: none;
          .lg-block({
                display: block;
                float: left;
                width: 200px;
            }); 
        }
        &--tab {
           .lg-block({
                display: none;
            }); 
        }
    }
    &_right {
        margin-top: 20px;
        &--title {
            .philbold();
            font-size: 51px;
            min-width: 400px;
            margin-bottom: 45px;
        }
        &--subt {
            &2 {
                margin-top: 65px;
                .lg-block({
                    margin-top: 45px;
                }); 
            }
            .phil();
            font-size: 22px;
        }
        &--desc {
            .light();
            font-size: 15px;
            max-width: 360px;
            margin-top: 15px;
        }
    }
}