.set {
    padding-top: 50px;
    text-align: center;
    &_title {
        .h2();
        color: #fff;
        &::after {
            .h2_after();
        }
    }
    &_wrap {
        margin-top: 45px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }
    &_left {
        .row-flex();
        align-items: flex-start;
    }
    &_item {
        margin: 10px 0;
        .col();
        .size(3);
        &--img {
            height: 141px;
            position: relative;
            img {
                position: absolute;
                bottom: 0; left: 50%;
                transform: translate(-50%,0);
            }
            .special {
                left: 40%;
            }
        }
        &--title {
            margin-top: 40px;
            color: #ddba3f;
            font-size: 14px;
            padding: 0 25px;
        }
        &--desc {
            margin-top: 20px;
            color: #fff;
            font-size: 12px;
        }
    }
    &_right {
        color: #fff;
        .lg-block({
            margin: 35px auto 0;
        });
        &--img {
            .lg-block({
                width: 300px;
            });
        }
        &--p {
            margin-top: 70px;
            .phil();
            font-size: 18px;
            line-height: 1.5;
            .lg-block({
                max-width: 750px;
            });
        }
        &--btn {
            margin-top: 60px;
            .btn();
        }
    }
}