@import "smart-grid.less";
@import "fonts.less";
@import "header.less";
@import "intro.less";
@import "set.less";
@import "about.less";
@import "gray.less";
@import "already.less";
@import "gallery.less";
@import "trust.less";
@import "people.less";
@import "know.less";
@import "why.less";
@import "map.less";
@import "offer.less";
@import "footer.less";

.reset();
.container {
    .container();
    .xmd-block({
        max-width: 768px;
    });
}

html {
    font-size: 16px;
}
a {
    text-decoration: none;
    color: inherit;
}
a:hover {
    text-decoration: none;
}
body {
    font-family: 'MullerRegular', 'Helvetiсa', sans-serif;
    background: url(../img/fon.png) no-repeat;
    background-size: cover;
    background-position: center;
}
ul,li {
    margin: 0;
    padding: 0;
}
li {
    list-style: none;
}
:active, :hover, :focus {
    outline: 0;
    outline-offset: 0;
}
input {
    border: none;
}
button {
    background: none;
    border: none;
}
* {
    box-sizing: border-box;
}

.medium() {
    font-family: 'MullerMedium', 'Helvetiсa', sans-serif;
}
.bold() {
    font-family: 'MullerBold', 'Helvetiсa', sans-serif;
}
.extrabold() {
    font-family: 'MullerExtraBold', 'Helvetiсa', sans-serif;
}
.light() {
    font-family: 'MullerLight', 'Helvetiсa', sans-serif;
}
.phil() {
    font-family: 'PhilosopherRegular', 'Helvetiсa', sans-serif;
}
.philbold() {
    font-family: 'PhilosopherBold', 'Helvetiсa', sans-serif;
}
.h2() {
    .phil();
    font-size: 60px;
    position: relative;
    padding-bottom: 15px;
}
.h2_after() {
    content: '';
    position: absolute;
    bottom: 0; left: 50%;
    transform: translate(-50%,0);
    width: 170px;
    height: 2px;
    background-color: #ffd956;
}
.btn() {
    display: inline-block;
    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.8);
    border-radius: 35px;
    background-color: #787878;
    background-image: linear-gradient(to top, #951818 0%, #ff2727 100%);
    position: relative;
    padding: 25px 50px;
    text-align: center;
    width: 370px;
    transition: .5s all;
    &::after {
        content: '';
        position: absolute;
        top: 50%; right: 50px;
        transform: translate(0,-50%);
        background: url(../img/btn_basket.png) no-repeat;
        width: 26px;
        height: 24px;
    }
    &:hover {
        .hover();
    }
}
.outline {
    border: 1px solid rgba(5,118,188,0.7);
}
.hover() {
    transform: scale(1.1);
}
@keyframes blink{
    0%{top: 12px;left: 0;}
    100%{top: 12px;left: 80%;}
}
.blink {
    top: 12px;
    display: none;
    animation: blink 1s;
    animation-iteration-count: infinite;
    position: absolute;
    background-color: #fff;
    width: 20px;
    height: 45px;
    transform: skewX(-30deg);
    z-index: 10;
}
.intro__form--btn:hover .blink {
    display: block;
}
.btn_mnu {
    width: 50px;
    height: 50px;
    display: -webkit-flex;
    display: -moz-flex;
    display: -ms-flex;
    display: -o-flex;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: transparent;
    cursor: pointer;
    position: relative;
    position: absolute;
    top: 15px; left: 20px;
    z-index: -1;
    opacity: 0;
    .xmd-block({
        z-index: 11;
        opacity: 1;
    });
}
.btn_row {
    height: 3px;
    border-radius: 2px;
    width: 82%;
    margin: 2px 0;
    background-color: #fff;
    position: relative;
    -webkit-transition: all .5s;
    -o-transition: all .5s;
    transition: all .5s;
}
.btn_mnu.active .btn_row:first-child {
    -webkit-transform: rotate(-135deg);
    -ms-transform: rotate(-135deg);
    -o-transform: rotate(-135deg);
    transform: rotate(-135deg);
    top: 6px;
}
.btn_mnu.active .btn_row:last-child {
    -webkit-transform: rotate(-225deg);
    -ms-transform: rotate(-225deg);
    -o-transform: rotate(-225deg);
    transform: rotate(-225deg);
    top: -6px;
}
.btn_mnu.active .btn_row:nth-child(2) {
    opacity: 0;
}