.people {
    background: url(../img/people.png) no-repeat;
    background-position: center;
    min-height: 761px;
    margin-top: -30px;
    &_content {
        max-width: 670px;
        margin-left: auto;
        padding-top: 110px;
        text-align: center;
    }
    &_ul {
        
    }
    &_li {
        padding-left: 65px;
        position: relative;
        margin:  30px 0;
        &::before {
            content: '';
            position: absolute;
            top: 0; left: 0;
            background: url(../img/people-li1.png) no-repeat;
            width: 43px;
            height: 40px;
        }
        &:last-child {
            margin-bottom: 50px;
        }
        p {
            .medium();
            font-size: 18px;
            text-align: left;
        }
    }
    &_btn {
        margin: 0 auto;
        .btn();
        color: #fff;
    }
}

