.trust {
    margin-top: 210px;
    text-align: center;
    color: #fff;
    background: url(../img/trust.png) no-repeat;
    background-position: center;
    min-height: 700px;
    .lg-block({
        margin-top: 110px; 
    });     
    &_title {
        padding-top: 70px;
        .h2();
        &::after {
            .h2_after();
        }
    }
    &_text {
        .phil();
        font-size: 22px;
    }
    &_wrap {
        margin-top: 40px;
        display: flex;
        justify-content: center;
        align-items: flex-end;
        flex-wrap: wrap;
        &::before, &::after {
            content: '';
            width: 100%;
            order: 1;
            .lg-block({
                display: none;
            }); 
        }
    }
    &_item {
        margin: 40px;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        .lg-block({
            margin: 20px;
        }); 
        &:nth-child(n + 5) {
            order: 1;
            .lg-block({
                order: 0;
            }); 
        }
    }
    
}