.know {
    color: #fff;
    padding-top: 35px;
    overflow-x: hidden;
    &_title {
        text-align: center;
        .h2();
        &::after {
            .h2_after();
        }
    }
    &_content {
        position: relative;
    }
    &_left {
        &__top {
            max-width: 880px;
        }
        &__bottom {
            margin-top: 65px;
            margin-left: -22px;
            .lg-block({
                margin-top: 25px;
            });
            img {
                position: relative;
                z-index: 2;
            }
        }
        &--li {
            position: relative;
            padding-left: 120px;
            margin-top: 60px;
            p {
                .medium();
                font-size: 18px;
                max-width: 740px;
                line-height: 1.5;
                .lg-block({
                    max-width: 550px;
                });
            }
            &:nth-child(1) {
                &::before {
                    content: '';
                    position: absolute;
                    top: -15px; left: 0;
                    background: url(../img/knowli1.png) no-repeat;
                    width: 100px;
                    height: 70px;
                }
            }
            &:nth-child(2) {
                &::before {
                    content: '';
                    position: absolute;
                    top: 0px; left: 0;
                    background: url(../img/knowli2.png) no-repeat;
                    width: 99px;
                    height: 72px;
                }
            }
        }
    }
    &_right {
        position: absolute;
        width: 826px;
        height: 777px;
        top: -80px; right: -200px;
        .lg-block({
            width: 498px;
            height: 468px;
            right: 0;
        });
        img {
            position: relative;
            z-index: 1;
        }
    }
}
